import { Center } from "@chakra-ui/react";
import React from "react";
import GitHubCalendar from "react-github-calendar";
import styles from "../Styles/Github.module.css";
import ReactTooltip from "react-tooltip";

const Github = () => {
  const selectLastHalfYear = (contribution) => {
    const currentYear = new Date().getFullYear();
    const currentMonth = new Date().getMonth();
    const showMonths = 7;

    return contribution.filter((day) => {
      const date = new Date(day.date);
      const monthOfDay = date.getMonth();

      return (
        date.getFullYear() === currentYear &&
        monthOfDay > currentMonth - showMonths &&
        monthOfDay <= currentMonth
      );
    });
  };
  return (
    <div className={styles.github}>
        <div className={styles.calender} id="github">
        <Center
            m="auto"
            data-aos="zoom-in"
            fontSize={{ base: "25px", sm: "25x", md: "30px", lg: "35px" }}
            fontWeight="bold"
            borderBottom="solid 4px rgba(255, 115, 0, 0.801)"
            paddingBottom="0px"
            w={{ base: "230px", sm: "230px", md: "280px", lg: "330px" }}
        >
            GITHUB CALENDER
        </Center>
        <div className={styles.calenderChild}>
            <GitHubCalendar
            username="himanku"
            transformData={selectLastHalfYear}
            blockSize={20}
            fontSize={15}
            blockRadius={2}
            >
            <ReactTooltip delayShow={20} html />
            </GitHubCalendar>
        </div>
        </div>
    </div>
  );
};

export default Github;
